body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
    margin: 0;
}

html, body {
    height: 100%;
}

a {
    text-decoration:none;
    color: inherit;
}

#root {
    height:100%;
}

.Screen {
    margin: 0 auto;
    width: 560px;
    height: 900px;
    border: 1px solid black;
    background-color: #FFE5B4;
    display:grid;
    grid-template-rows: 1fr;
    grid-template-columns: 1fr;
}

@media all and (max-width: 1000px) and (min-height: 320px) {
    .Screen {
        width: 100%;
        height: 100%;
    }
}

.App {
    text-align: center;
    background-color: #333333;
    height:100%;
    font-family: 'Montserrat', sans-serif;
}

h1, h2, h3, h4, h5, h6 {
    font-family: 'Montserrat', sans-serif;
}
.FaultReport {
    display: grid;
    grid-template-rows:20% 10% auto;
}

.FaultReport img {
    width:50%;
    margin: auto;
}

.FaultReportTitle {
    color: #682C91;
}

.FaultReportButton {
    width: 30%;
    /*height: 50px;*/
}

.FaultReportText textarea {
    font-size:15px;
}

.FaultReportHeader {
    font-size: 30px;
}

.FaultReportQuestions {
    display:grid;
    grid-template-rows: 20% 1fr;
    -webkit-align-items: end;
            align-items: end;
}

.FaultReportQuestionsChoices {
    /*height:30%;*/
    display:grid;
    grid-template-columns: 1fr 1fr;
    background-color: #682C91;
    height:250px;
}

.FaultReportQuestionsChoices:before {
    border-radius: 0px 0px 100% 100%;
    position:relative;
    height: 100px;
    top: 0px;
    background-color: #FFE5B4;
    content:'';
    grid-column: 1 / span 2;
}

.FaultReportQuestionsItem {
}

.FaultReportQuestionsItem div:hover {
    color: #AAAAAA;
}

.FaultReportQuestionsItem:active {
    color: #aaaaaa;
    -webkit-animation: fadeinout 4s linear forwards;
    animation: fadeinout 4s linear forwards;
}

.FaultReportQuestionsQuestion {
    font-size:30px;
}
.FaultReportQuestionsItemText {
    font-size:40px;
}

.FaultReportResult {
    font-size:20px;
}

.FaultReportResultInput textarea {
    resize: none;
    font-size:16px;
}

.FaultReportResultInput button {
    padding: 14px;
    font-size:20px;
}


@-webkit-keyframes fadeinout {
  100% { opacity: 1; }
  50% { opacity: 0.5; }
  0% { opacity: 0; }
}

@keyframes fadeinout {
  100% { opacity: 1; }
  50% { opacity: 0.5; }
  0% { opacity: 0; }
}

